import React from "react";
import Fade from "react-reveal/Fade";
import FadeLoader from "react-spinners/FadeLoader";
import classNames from "classnames";
import "./mystyles.scss";

const GameModal = props => {
  return (
    <Fade duration={200}>
      <div className="modal is-active">
        <div className="modal-background" />
        <div
          className="modal-content"
          style={{ width: "auto", height: "auto" }}
        >
          {props.children}
        </div>
        <button
          className="modal-close is-large"
          ariaLabel="close"
          onClick={props.onClose}
        />
      </div>
    </Fade>
  );
};

const DetailModal = props => {
  return (
    <Fade duration={200}>
      <div className="modal is-active">
        <div className="modal-background" onClick={props.onClose} />
        <div className="modal-content" style={{ maxHeight: "100%" }}>
          {props.children}
        </div>
        <button
          className="modal-close is-large"
          ariaLabel="close"
          onClick={props.onClose}
        />
      </div>
    </Fade>
  );
};

const GameCard = props => {
  return (
    <Fade bottom distance="1.5em" duration={1500}>
      <div className="column is-half is-one-third-widescreen">
        <div className="card">
          <div className="card-image">
            <figure className="image">
              <img src={props.cover} alt="cover" />
            </figure>
          </div>
          <div className="card-content">
            <div className="content">
              <p className="title">{props.title}</p>
              <p className="subtitle has-text-grey">{props.genre}</p>
              {props.children}
            </div>
          </div>
        </div>
      </div>
    </Fade>
  );
};

class DetailCard extends React.Component {
  state = {
    imageLoaded: false
  };

  renderImage() {
    return (
      <a
        className={classNames({ "is-hidden": !this.state.imageLoaded })}
        href={this.props.img}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img
          src={this.props.img}
          alt={this.props.alt}
          style={{
            maxHeight: window.innerHeight / 2,
            width: "auto",
            display: "inline-block"
          }}
          onLoad={() => this.setState({ imageLoaded: true })}
        />
      </a>
    );
  }

  renderLoader() {
    if (this.state.imageLoaded) return null;
    return (
      <div style={{ minHeight: window.innerHeight / 3 }}>
        <FadeLoader
          size={100}
          color={"#FFF"}
          css={{
            display: "block",
            position: "absolute",
            top: "50%",
            left: "50%"
          }}
        />
      </div>
    );
  }

  render() {
    return (
      <Fade>
        <div className="card" style={{ margin: 20, borderRadius: 5 }}>
          <div className="card-image">
            <figure
              className="image"
              style={{ backgroundColor: "#000", textAlign: "center" }}
            >
              {this.renderLoader()}
              {this.renderImage()}
            </figure>
          </div>
          <div className="card-content">{this.props.children}</div>
        </div>
      </Fade>
    );
  }
}

class VideoCard extends React.Component {
  constructor(props) {
    super(props);
    this.video = React.createRef();
  }

  render() {
    return (
      <Fade>
        <div className="card" style={{ margin: 20, borderRadius: 5 }}>
          <div className="card-image">
            <figure
              className="image"
              style={{ backgroundColor: "#000", textAlign: "center" }}
            >
              <video
                src={this.props.src}
                autoPlay
                loop
                playsInline
                muted
                style={{
                  maxHeight: window.innerHeight / 2,
                  width: "auto",
                  display: "inline-block"
                }}
                ref={this.video}
                onClick={() => {
                  let video = this.video.current;
                  if (video.paused) {
                    video.play();
                  } else {
                    video.pause();
                  }
                }}
              />
            </figure>
          </div>
          <div className="card-content">{this.props.children}</div>
        </div>
      </Fade>
    );
  }
}

